<template>
  <div class="skills">
      <mu-list>
        <mu-list-item toggleNested :open="open" id="mucss" @click="z">
          <h1>技术栈</h1> 
             <mu-list-item slot="nested" >
               <h2 id="hcss">HTML&nbsp;&nbsp;&nbsp;CSS3</h2>
               <h2 id="hcss">javaScript</h2>
               <h2 id="hcss">Vue&nbsp;&nbsp;&nbsp;jquery</h2>
               <h2 id="hcss">Node&nbsp;&nbsp;&nbsp;ES6</h2>
               <h2 id="hcss">svn&nbsp;&nbsp;&nbsp;git</h2>
               <h2 id="hcss">ESlint&nbsp;&nbsp;&nbsp;webpack</h2>
               <h2 id="hcss">微信公众号</h2>
          </mu-list-item>
        </mu-list-item>
        <mu-list-item toggleNested :open="open2" @click="zz">
          <h1>工作经历</h1>
             <mu-list-item slot="nested">
               <h2  id="hcss2">2015.08 ~2016.12</h2>
               <h2 id="hcss2">烟台佳创网络科技有限公司</h2>
               <h2 id="hcss2">主要使用div+css+jq</h2>
               <h2 id="hcss2">负责网站前端页面实现</h2>
               <h2 id="hcss2">2017.03 ~至今</h2>
               <h2 id="hcss2">苏州益发特科技有限公司</h2>
               <h2 id="hcss2">主要使用vue+ES6+node</h2>
               <h2 id="hcss2">负责项目页面实现后端数据交互</h2>
             </mu-list-item>
        </mu-list-item>
        <mu-list-item toggleNested :open="open3" @click="zzz">
          <h1>自我评价</h1>
          <mu-list-item slot="nested">
            <h2 id="hcss3">一个性格幽默、做事认真的人</h2>
            <h2 id="hcss3">喜欢研究碰到的各种新技术</h2>
            <h2 id="hcss3">善于沟通有较强的团队交流能力</h2>
            <h2 id="hcss3">对工作充满热情, 吃苦耐劳</h2>
            <h2 id="hcss3">能在较大的压力下保持良好的工作状态</h2>     
          </mu-list-item>
        </mu-list-item>
      </mu-list>
      <gAnimation v-if="open3 || !open&&!open2&&!open3"></gAnimation>
  </div>
</template>
<script>
  import gAnimation from './gAnimation'
  export default {
    name: 'skills',
    data () {
      return {
        open: false,
        open2: false,
        open3: false
      }
    },
    methods: {
      z () {
        if (!this.open) {
          this.open = true
          this.open2 = false
          this.open3 = false
        } else {
          this.open2 = true
          this.open = false
          this.open3 = false
        }
      },
      zz () {
        if (!this.open2) {
          this.open2 = true
          this.open3 = false
          this.open = false
        } else {
          this.open3 = true
          this.open2 = false
          this.open = false
        }
      },
      zzz () {
        if (!this.open3) {
          this.open3 = true
          this.open2 = false
          this.open = false
        } else {
          this.open = true
          this.open2 = false
          this.open3 = false
        }
      }
    },
    components: {
      gAnimation
    }
  }
</script>
<style>
  @keyframes css1 {
    0%{
      opacity: 0;
      color: black;
    }
    25%{
      opacity: .6;
      color: red;
    }
    50%{
      opacity: .7;
      color: yellow;
    }
    75%{
      opacity: .8;
      color: orange;
    }
    100%{
      opacity: 1;
      color: #f3ebeb;
    }
  }
  @keyframes css2 {
    0% {
      transform: rotate(0);
    }
    100% {
      transform: rotate(720deg);
    }
  }
  @keyframes css3 {
    0% {
      transform: scale(0)
    }
    100% {
      transform: scale(1)
    }
  }
  #hcss{
    animation: css1 2s;
    -webkit-animation: css1 2s;
  }
  #hcss2{
    animation: css2 2s;
  }
  #hcss3{
    animation: css3 2s;
  }
</style>



