<template>
  <div class="loading">
    <img src="http://ovqf9bvrg.bkt.clouddn.com/atm.png" alt="" id="loadingImg">
  </div>
</template>
<script>
  export default {
    name: 'loading',
    methods: {
      handleTabChange (val) {
        this.activeTab = val
      }
    },
    mounted () {
      setTimeout(() => {
        this.$router.push({name: 'about_me'})
      }, 3000)
    }
  }
</script>
<style lang="">
	@keyframes index{
		from{
			width: 35%;
			opacity: 1;
			transform: rotate(0);
		}
		to{
			width: 0;
			opacity: 0;
			transform: rotate(720deg);
		}
  }
  @-webkit-keyframes index{
		from{
			width: 35%;
			opacity: 1;
			transform: rotate(0);
		}
		to{
			width: 0;
			opacity: 0;
			transform: rotate(720deg);
		}
	}
  .loading{
		width: 100vw;
		height: 100vh;
		display: -webkit-flex;
		display: flex;
		justify-content: center;
		align-items: center;
	}
  #loadingImg{
			width: 20%;
      animation: index 3s ease-out 1s;
      -webkit-animation: index 3s ease-out 1s;
		}
</style>



