<template>
<mu-paper>
  <mu-bottom-nav :value="bottomNav" @change="handleChange" id="list-nav">
    <mu-bottom-nav-item to='/about_me' value="about me" title="me" />
    <mu-bottom-nav-item to='/skills' value="skills" title="skills"  />
    <mu-bottom-nav-item to='/project' value="project" title="project" />
  </mu-bottom-nav>
</mu-paper>
</template>
<script>
export default {
  name: 'list',
  data () {
    return {
      bottomNav: 'about me'
    }
  },
  methods: {
    handleChange (val) {
      this.bottomNav = val
    }
  }
}
</script>
<style lang="css">
  #list-nav{
    position: absolute;
    bottom: 0;
    z-index: 99999;
    background-color: #eee;
  }
  .tab-color{
    background-color: #007ACC;
  }
  .mu-tab-link-highlight{
    background-color: #000
  }
  .mu-bottom-item-text{
    font-size: 20px;
    line-height: 40px;
  }

</style>



