<template>
  <div class="gAnimation">
    <span></span>
    <span></span>
    <span></span>
    <span></span>
    <span></span>
  </div>
</template>

<script>
  export default{
    name: 'gAanimation'
  }
</script>
<style>
  .gAnimation{
    width: 80px;
    height: 20px;
    margin: 0 auto;
  }
  .gAnimation span{
    display: inline-block;
    width: 8px;
    height: 100%;
    border-radius: 4px;
    background: lightgreen;
    -webkit-animation: load 1s ease infinite;
  }
  @-webkit-keyframes load{
      0%,100%{
          height: 20px;
          background: #4D71BB;
      }
      30%{
          height: 30px;
          background: #E6FC31;
      }
      40%{
          height: 40px;
          background: #FBC131;
      }
      50%{
          height: 50px;
          background: #DD3E46;
      }
      60%{
          height: 40px;
          background: #4FA4BF;
      }
      70%{
          height: 30px;
          background: #4FA4BF;
      }
      80%{
          height: 25px;
          background: #E36B39;
      }
  }
  .gAnimation span:nth-child(2){
      -webkit-animation-delay:0.2s;
  }
  .gAnimation span:nth-child(3){
      -webkit-animation-delay:0.4s;
  }
  .gAnimation span:nth-child(4){
      -webkit-animation-delay:0.6s;
  }
  .gAnimation span:nth-child(5){
      -webkit-animation-delay:0.8s;
  }
</style>



