<template>
  <div id="app">
    <router-view></router-view>
    <list v-if="$route.path !=='/'"></list>
  </div>
</template>
<script>
  import list from './components/list'
  export default {
    name: 'app',
    components: {
      list
    }
  }
</script>
<style lang="">
  html,body,#app{
    width: 100%;
    height: 100%;
  }
  #app {
    position: relative;
    background: url(http://ovqf9bvrg.bkt.clouddn.com/bg2.jpg)no-repeat;
    background-size: 100% 100%;
  }
</style>



