<template>
  <div class="about_me">
    <h1 v-html="zz"></h1>
    <gAnimation v-if="istrue"></gAnimation>
  </div>
</template>

<script>
  // 声明 全局变量 f 用于清除定时器
  var f
  import gAnimation from './gAnimation'
  export default{
    name: 'about_me',
    data () {
      return {
        text: [],
        index: 0,
        zz: '',
        istrue: false
      }
    },
    components: {
      gAnimation
    },
    methods: {
      show () {
        // ES6 字符串拼接
        // ES6 使用变量 ${}
        let gYear = new Date().getFullYear() - 1993
        let gMonth = new Date().getMonth() + 0 - 7
        let gDate = new Date().getDate()
        let gHours = new Date().getHours()
        let gMinutes = new Date().getMinutes()
        let gSeconds = new Date().getSeconds()
        let content = `
          <h1 id="c3">Keep dreaming</h1>
          <h1>姓名：高权</h1>
          <h1>性别：男</h1>
          <h1>籍贯：山东</h1>
          <h1>年龄：${gYear}岁${gMonth}个月${gDate}天</h1>
          <h1>${gHours}小时${gMinutes}分钟${gSeconds}秒</h1>
          <h1>毕业于烟台大学文经学院</h1>
          <h1>计算机科学与技术专业</h1>
          <h1>手机：17602178970</h1>
          <h1>邮箱：kevinl7@163.com</h1>
          <h1>github : <a href='https://github.com/Kevinl7' target='_blank'>点击进入</a></h1>
        `
        this.zz = content.substring(0, this.index++)
      }
    },
    mounted () {
      f = setInterval(() => {
        this.show()
      }, 100)
    },
    watch: {
      index (nv, oldv) {
        if (nv >= 399) {
          window.clearInterval(f)
          this.index = 0
          // this.istrue = true
        }
      }
    }
  }
</script>
<style lang="css">
  #c3{
    font-size: 30px;
    color: #fff;
    padding: 10px 0;
    text-shadow:0 0 5px #CCCCCC, 0 0 10px #CCCCCC, 0 0 15px #CCCCCC, 0 0 20px #095816, 0 0 25px #095816, 0 0 30px #095816, 0 0 50px #095816, 0 0 80px #095816, 0 0 100px #095816, 0 0 150px #095816;
  }

</style>



